import { StringOrNull } from '../Base';

import { MAX_IMAGE_PATH_LENGTH, NonEmptyString } from './BaseString';

export class ImagePath extends NonEmptyString<'ImagePath', StringOrNull> {
  static from(value: StringOrNull = null): ImagePath {
    return new ImagePath(value);
  }
  protected validate(value: StringOrNull): StringOrNull {
    value = super.validate(value);

    if (value !== null && value.length > MAX_IMAGE_PATH_LENGTH) {
      throw new Error(
        `ImagePath has maximum length of ${MAX_IMAGE_PATH_LENGTH}`,
      );
    }

    return value;
  }
}
